.container {
  display: flex;
  width: 100%;
  overflow: hidden;
}
.logo {
  display: flex;
  align-items: center;
  flex: 1;
  gap: 1rem;
}
.logo > img {
  width: 2.5rem;
  height: 2.5rem;
}
.logo > span {
  font-weight: 600;
}
.right {
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}

.menu {
  display: flex;
  gap: 2rem;
  font-weight: 500;
  list-style: none;
  padding-right: 1rem;
}
.menu > li:hover {
  color: #81c784;
  cursor: pointer;
}

.search {
  width: 7rem;
  outline: none;
  height: 30%;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
}

.cart {
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
}

@media screen and (max-width: 640px) {
  .menu {
    padding-right: 0rem;
  }
}
